<template>
	<div class="tab_box whitebg">
		<div class="tab_buttons">
			<ul>
				<li v-for="(item,index) in articleTabNav" :class="[ index === 0? 'newscurrent' : '' ]" >{{item.typeName}}</li>
			</ul>
		</div>
		<div class="newstab" >
			<div class="newsitem" v-for="(item,index) in articleTabNav">
				<div class="newspic">
					<ul>
						<li><a href="/"><img :src="item.imgUrl1"><span>个人博客，属于我的小世界！</span></a></li>
						<li><a href="/"><img :src="item.imgUrl2"><span>个人网站做好了，百度不收录怎么办？来，看看他们怎么做的</span></a></li>
					</ul>
				</div>
				<ul class="newslist">
					<li v-for="(item2,index2) in item.list">
						<i></i><router-link :to = "{name:'articleDetail',query:{id:1}}">{{item2.title}}</router-link></a>
						<p>{{item2.articleAbstract}}</p>
					</li>
				</ul>
			</div>
			
		</div>
	</div>
	</div>

</template>

<script>
	import {mapState,mapActions} from 'vuex'
	export default {
		computed:{
			...mapState(['articleTabNav']),
			
		},
		mounted() {
		},
		data() {
			return {
			}
		},
		methods:{
			
		}
	}
</script>

<style>
</style>



