import {post, get} from '@/axios/index';

const actions = {
	addFun(context,param){
		context.commit("ADD_COUNT",param)
	},
	reduceFun(context){
		context.commit("REDUCE_COUNT")
	},
	updateSearchBox(context){
		context.commit('UPDATE_SEARCHBOX')
	},
	fetchFullArticleInfo(context,param){
		let payload;
		get(`/articleInfo/getArticleInfo?id=${param}`,{}).then((res)=>{
			 payload = res.data
			 context.commit('UPDATE_ARTICLE_INFO',payload)
			 
		});
		
	}
}
export default actions


