const mutations = {
	ADD_COUNT(state,param){
		alert('param'+param)
		state.count = state.count + 1
	},
	REDUCE_COUNT(state){
		state.count = state.count - 1
	},
	UPDATE_SEARCHBOX(state){
		state.searchBoxHidden = !state.searchBoxHidden
	},
	UPDATE_ARTICLE_INFO(state,payload){
		state.articleFullContent = payload.content
		state.userInfo = payload.userInfo

	}
}
export default mutations



