<template>
	<div class="searchbox" v-if="searchBoxHidden">
		<div class="search open">
			<form action="/e/search/index.php" method="post" name="searchform" id="searchform">
				<input name="keyboard" id="keyboard" class="input_text" value="请输入关键字词" style="color: rgb(153, 153, 153);" onFocus="if(value=='请输入关键字词'){this.style.color='#000';value=''}"
				 onBlur="if(value==''){this.style.color='#999';value='请输入关键字词'}" type="text">
				<input name="show" value="title" type="hidden">
				<input name="tempid" value="1" type="hidden">
				<input name="tbname" value="news" type="hidden">
				<input name="Submit" class="input_submit" value="搜索" type="submit">
			</form>
		</div>
		<div class="searchclose" @click="updateSearchBox"></div>
	</div>
</template>

<script>
	import {mapState,mapActions} from 'vuex'
	export default {
		computed:{
			...mapState(['searchBoxHidden']),
		},
		data() {
			return {
				
			}
		},
		methods: {
			...mapActions(['updateSearchBox'])
		}
		
	}
</script>

<style>

</style>



